<template>
  <div class="about">
    <transition name="fade">
      <div id="progress-bar-container" v-if="showProgressBar">
        <b-row style="padding-bottom: 12px">
          <b-col cols="12">
            <span class="text-center" id="cunt">Thank you!</span>
          </b-col>
        </b-row>
        <b-row>
          <b-progress id="progress-bar" :value="points % 10" :max="10" show-value animated></b-progress>
        </b-row>
      </div>
    </transition>
    <div id="googleMap" style="width:100%;height:720px;" />
    <md-button
      class="md-fab md-primary"
      v-if="!showSidebar && !selectingLocation"
      id="add-submission-button"
      @click="startSubmission"
    >
      <md-icon>add</md-icon>
    </md-button>
    <md-button
      class="md-fab md-primary"
      v-if="selectingLocation"
      id="add-submission-button"
      @click="savePin"
    >
      <md-icon>check</md-icon>
    </md-button>
    <md-button
      v-if="!showSidebar && !selectingLocation && ! showSubmissionPopup"
      id="burger"
      @click="showSidebar = true"
      class="md-fab md-primary"
    >
      <md-icon id="burger-icon">menu</md-icon>
    </md-button>
    <transition name="slide-vert">
      <SubmissionPopup
        v-if="showSubmissionPopup"
        @pinChosen="addPin"
        @submissionPopupClosed="showSubmissionPopup = false"
      />
    </transition>
    <transition name="slide-fade">
      <Sidebar
        v-if="showSidebar"
        @overlaysChosen="connectDataset"
        :selected="selectedDataset"
        @sidebarClosed="showSidebar = false"
        :points="points"
      />
    </transition>
  </div>
</template>

<script>
import SubmissionPopup from "../components/SubmissionPopup";
import Sidebar from "../components/Sidebar";

export default {
  components: {
    SubmissionPopup,
    Sidebar
  },
  data() {
    return {
      // ui garbage
      showSubmissionPopup: false,
      showSidebar: false,
      selectingLocation: false,
      selectedDataset: "none",
      showProgressBar: false,

      map: {},
      mapProp: {},
      coords: [126.3832, 5.9775, 111.16],
      db: {},
      dataset: {},
      heatmap: {},
      marker: {},
      typeEmojis: {
        oilSpill: "🛢️",
        bottles: "🧴",
        benchiks: "🚬",
        deadFish: "🐟",
        algaeBloom: "🍀",
        seaweed: "🌿"
      },
      pins: [],
      points: 0,
      warningMessages: {
        chl:
          "High chlorophyll concentration in your area! Check with your local authorities if swimming is advised!",
        temp:
          "Water temperature may not be adequate for swimming! Please evaluate your abilities to handle cold water."
      }
    };
  },
  methods: {
    savePin() {
      this.mapClickHandler(this.map, this.marker);
      this.marker.setMap(null);
      this.selectingLocation = false;
      this.showProgressBar = true;
      setTimeout(() => {
        this.points++;
        setTimeout(() => {
          this.showProgressBar = false;
        }, 2000);
      }, 200);
    },

    addPin(type) {
      // add pin here
      // navigator.geolocation.getCurrentPosition(p => {
      this.togglePins(false);

      this.marker = new google.maps.Marker({
        // position: new google.maps.LatLng(p.coords.latitude, p.coords.longitude),
        position: new google.maps.LatLng(58.35943, 24.312365),
        draggable: true,
        label: { text: this.typeEmojis[type], fontSize: "16px" }
      });
      this.marker.setMap(this.map);
      this.marker.wavesType = type;
      // });
      this.showSubmissionPopup = false;
      this.selectingLocation = true;
    },

    startSubmission() {
      this.showSubmissionPopup = true;
    },

    drawHeatmap(coordsList) {
      var heatmapData = [];

      coordsList.forEach(child => {
        var c = child.val();
        if (c && c.lat && c.lng) {
          var pos = {
            location: new google.maps.LatLng(c.lat, c.lng),
            weight: c.value
          };
          heatmapData.push(pos);
        }
      });

      this.heatmap = new google.maps.visualization.HeatmapLayer({
        data: heatmapData,
        dissipating: false,
        radius: 0.0025,
        map: this.map,
        gradient: [
          "rgba(102, 255, 0, 0)",
          "rgba(102, 255, 0, 1)",
          "rgba(120, 255, 0, 1)",
          "rgba(147, 255, 0, 1)",
          "rgba(193, 255, 0, 1)",
          "rgba(210, 255, 0, 1)",
          "rgba(238, 255, 0, 1)",
          "rgba(244, 227, 0, 1)",
          "rgba(249, 198, 0, 1)",
          "rgba(255, 170, 0, 1)",
          "rgba(255, 113, 0, 1)"
        ]
      });
    },

    clearHeatmap() {
      if (this.heatmap.setMap) {
        this.heatmap.setMap(null);
      }
    },

    drawPins() {
      var clicks = this.db.ref("clicks");
      clicks.once("value").then(o => {
        o.forEach(item => {
          var m = item.val();
          this.drawPin(new google.maps.LatLng(m.lat, m.lng), m.type);
        });
      });
    },

    togglePins(show) {
      for (var i = 0; i < this.pins.length; i++) {
        if (show === true) {
          this.pins[i].setMap(this.map);
        } else {
          this.pins[i].setMap(null);
        }
      }
    },

    drawPin(pos, type) {
      if (!type) return;

      var m = new google.maps.Marker({
        position: pos,
        label: { text: this.typeEmojis[type], fontSize: "16px" }
      });
      m.setMap(this.map);

      this.togglePins(true);
      this.pins.push(m);
    },

    mapClickHandler(map, marker) {
      var data = {
        lat: marker.position.lat(),
        lng: marker.position.lng(),
        timestamp: new Date(),
        type: marker.wavesType
      };

      this.saveData(data);
      this.drawPin(marker.position, marker.wavesType);
    },

    getTimestamp(addClick) {
      // Reference to location for saving the last click time.
      var ref = this.db.ref("last_message/me");

      ref.onDisconnect().remove(); // Delete reference from firebase on disconnect.

      // Set value to timestamp.
      ref.set(firebase.database.ServerValue.TIMESTAMP, function(err) {
        if (err) {
          // Write to last message was unsuccessful.
          console.log(err);
        } else {
          // Write to last message was successful.
          ref.once(
            "value",
            function(snap) {
              addClick(snap.val()); // Add click with same timestamp.
            },
            function(err) {
              console.warn(err);
            }
          );
        }
      });
    },

    saveData(data) {
      console.log("push");
      this.getTimestamp(timestamp => {
        // Add the new timestamp to the record data.
        data.timestamp = timestamp;
        var ref = this.db.ref("clicks").push(data, function(err) {
          if (err) {
            // Data was not written to firebase.
            console.warn(err);
          }
        });

        var points = this.db
          .ref("points")
          .push({ timestamp: timestamp }, function(err) {
            if (err) {
              // Data was not written to firebase.
              console.warn(err);
            }
          });
      });
    },

    connectDataset(name) {
      if (name === "none") {
        this.clearHeatmap();
      } else {
        this.dataset = this.db.ref(name);
        this.readData();
        this.makeToast(name);
      }

      this.showSidebar = false;
      this.selectedDataset = name;
    },

    configure() {
      // Configure firebase
      var firebaseConfig = {
        apiKey: "AIzaSyCGeyKtC0TRDvHLT3_W47MGgIXj2V6BJ1Q",
        authDomain: "oceanhack-21d54.firebaseapp.com",
        databaseURL: "https://oceanhack-21d54.firebaseio.com",
        projectId: "oceanhack-21d54",
        storageBucket: "oceanhack-21d54.appspot.com",
        messagingSenderId: "1050120586563",
        appId: "1:1050120586563:web:b811f526fbe3a719641bf6",
        measurementId: "G-4DVZVHJ3ML"
      };
      firebase.initializeApp(firebaseConfig);
      this.db = firebase.database();

      // Configure Google maps
      // navigator.geolocation.getCurrentPosition(p => {
      this.mapProp = {
        // center: new google.maps.LatLng(p.coords.latitude, p.coords.longitude),
        center: new google.maps.LatLng(58.35943, 24.312365),
        zoom: 12.5,
        zoomControl: false,
        streetViewControl: false,
        fullscreenControl: false,
        mapTypeControl: false
      };

      this.map = new google.maps.Map(
        document.getElementById("googleMap"),
        this.mapProp
      );
      // });

      // Click handler
      google.maps.event.addListener(this.map, "click", event => {
        this.mapClickHandler(this.map, event.latLng);
      });
    },

    readData() {
      this.dataset.once("value").then(snapshot => {
        this.drawHeatmap(snapshot);
      });
    },

    makeToast(type) {
      this.$bvToast.toast(this.warningMessages[type], {
        autoHideDelay: 2000,
        variant: "danger",
        toaster: "b-toaster-top-left",
        title: "Warning"
      });

      setTimeout(() => {
        document.getElementsByClassName("toast")[0].style.width = "300px";
      }, 100);
    }
  },

  mounted() {
    this.configure();
    this.drawPins();

    this.points = 0;

    this.db
      .ref("points")
      .once("value")
      .then(o => {
        console.log("yeet");
        o.forEach(t => {
          this.points++;
        });
      });
  }
};
</script>

<style scoped>
#add-submission-button {
  /* height: 50px;
  width: 50px; */
  position: absolute;
  top: 620px;
  left: 330px;
  /* background: white; */
  /* border-radius: 30px; */
  /* cursor: pointer; */
  /* box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23); */
}

#burger {
  position: absolute;
  top: 10px;
  left: 335px;
  background: white;
}

#burger-icon {
  color: black !important;
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(200px);
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.3s ease;
}
.fade-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.fade-enter, .fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.slide-vert-enter-active {
  transition: all 0.3s ease;
}
.slide-vert-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-vert-enter, .slide-vert-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateY(200px);
  opacity: 0;
}

#progress-bar {
  margin-top: 20px;
  width: 94%;
  margin: auto;
  background-color: #e0efffaa;
}

#progress-bar-container {
  background: rgba(255, 255, 255, 0.9);
  height: 69px;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 999;
  width: 75%;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

#cunt {
  font-size: 24px;
}

.b-toaster-top-left div div div {
  width: 10px !important;
}
</style>