<template>
  <b-card id="sidebar">
    <b-row>
      <b-col class="text-center" cols="12" style="font-size: 50px; padding-top: 20px">👨</b-col>
    </b-row>
    <b-row>
      <b-col class="text-center" id="name-row" cols="12">
        <b>Jimmy Yeetington</b>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="text-center" id="level-row" cols="12">
        ⭐ Level {{Math.floor(points/10) + 1}},
        <b>Junior Sea Saviour</b>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="text-center" cols="12">{{10 - (points % 10)}} more reports until next level!</b-col>
    </b-row>
    <b-row>
      <b-col class="text-center" cols="12">
        <b-progress :value="points % 10" :max="10" show-value animated></b-progress>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="text-left" id="overlay-list-header" cols="12">Choose data overlays:</b-col>
    </b-row>
    <b-row id="overlay-list">
      <b-col cols="12">
        <b-form-group>
          <b-list-group class="text-left">
            <b-list-group-item>
              <b-form-radio v-model="result" name="some-radios" value="chl">Chlorophyll</b-form-radio>
            </b-list-group-item>
            <b-list-group-item>
              <b-form-radio v-model="result" name="some-radios" value="temp">Temperature</b-form-radio>
            </b-list-group-item>
            <b-list-group-item>
              <b-form-radio v-model="result" name="some-radios" value="none">None</b-form-radio>
            </b-list-group-item>
          </b-list-group>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row style="padding-top: 10px;">
      <b-col cols="6" class="text-left">
        <b-button @click="done" variant="success">✔️ Done</b-button>
      </b-col>
      <b-col cols="6" class="text-right">
        <b-button @click="$emit('sidebarClosed')" variant="info">❌ Close</b-button>
      </b-col>
    </b-row>
  </b-card>
</template>

<script>
export default {
  name: "Sidebar",
  data() {
    return {
      result: "none"
    };
  },
  props: ["selected", "points"],
  mounted() {
    this.result = this.selected;
  },
  methods: {
    done() {
      this.$emit("overlaysChosen", this.result);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
#sidebar {
  width: 70%;
  right: 0px;
  height: 100%;
  position: absolute;
  top: 0px;
  animation: slide-in 0.5s forwards;
  -webkit-animation: slide-in 0.5s forwards;
}

#level-row {
  font-size: 13px;
  padding-bottom: 10px;
}

#overlay-list {
  padding-top: 10px;
}

#overlay-list-header {
  padding-top: 20px;
}

#name-row {
  padding-bottom: 10px;
  font-size: 20px;
  padding-top: 30px;
}
</style>
